@import url(https://fonts.googleapis.com/css?family=Comfortaa|Patua+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  background: #F4EDED;
}

body,
button,
input,
select,
textarea {
	color: #404040;
	font-family: 'Comfortaa', Arial, sans-serif;
	font-size: 18px;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
  font-family: 'Patua One', serif;
  letter-spacing: 2px;
}

h2 {
  font-size: 1rem;
  color: #007489;
}

p {
	margin-bottom: 1.5em;
  color: #898989;
}

.cover {
  height: 400px;
  width: 100%;
  object-fit: cover;
}

.content{
  width: 100%;
  height: 100%;
  margin-top: 2em;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.center-content{
  width: 80%;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
	background: white;
	margin-bottom: 2em;	
	flex: 0 0 33%;
}

@media screen and (max-width: 600px) {
  .card {
    flex: 0 0 50%;
  }
}

.card img {
	display: block;
	border: 0;
	width: 100%;
  height: 200px;
  object-fit: cover;
}

.card a {
	color: black;
	text-decoration: none;
}

.card a:hover {
	box-shadow: 3px 3px 8px hsl(0, 0%, 80%);
}

.card-content {
  padding: 1.4em;
  position: relative;
}

.card-content h2 {
	margin-top: 0;
	margin-bottom: .5em;
	font-weight: bold;
}

.card-content p {
	font-size: 80%;
}

.recipepage {
  
  background: white;
  padding: 1.4em;
}

.recipepage h2 {
  font-size: 2rem;
  color: #007489;
}

.recipepage img {
  display: block;
	border: 0;
	width: auto;
  height: 500px;
  
}
.card-footer {
}
.card-tag {
margin-right: 0.5em;
border-radius: 15px;
background: #E2B4BD;
padding: 5px 10px;
font-family: 'Comfortaa', Arial, sans-serif;
font-size: 14px;
}
.Menu{
    width: 100%;
    border: 1px solid;
    min-height: 3.5em;
    background-color: #E2B4BD;
    color:#FFFFFA;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.MenuElement{
    margin-right: 2em;
    margin-left: 2em;
}
.menu-logo{
    display: block;
    position: absolute;
    width: auto;
    height: 4.5em;
    ;
}


